html * {
  font-family: "Poppins", sans-serif;
}
h1,
h2 {
  line-height: 1.2;
  margin-bottom: 0.3em;
  font-weight: bold;
}
h3,
h4,
h5,
h6,
p,
span,
div,
a {
  line-height: 130%;
}
h1 {
  font-size: 3rem;
}
h2 {
  font-size: 2rem;
}
ul {
  list-style: disc;
  margin-left: 1.25rem;
}
p,
li {
  font-size: 1rem;
  margin-bottom: 1rem;
  font-weight: 400;
  line-height: 1.5;
}

@media (max-width: 768px) {
  h1 {
    font-size: 2rem;
  }
}

@media (max-width: 576px) {
  p,
  li {
    font-size: 0.875rem;
  }
}
