button,
.btn {
  background: #5d2bff;
  box-shadow: none;
  position: relative;
  border: 1px solid #5d2bff;
  line-height: 1;
  text-transform: uppercase;
  padding: 0.75em 2em;
  text-decoration: none;
  font-size: 0.875rem;
  text-align: center;
  display: inline-block;
  color: #fff;
  border-radius: 0.5rem;
  box-shadow: 0 0 0.5rem rgba(93, 43, 255, 0.4);
  transition: 0.2s linear;
  > .material-symbols-outlined {
    margin-left: 0.25em;
    transition: 0.2s linear;
  }
  &:hover {
    cursor: pointer;
    color: #5d2bff;
    background: #f7f9fc;
    transition: 0.2s linear;
    .material-symbols-outlined {
      transform: rotate(180deg);
      transition: 0.2s linear;
    }
  }
  &.is-disabled {
    pointer-events: none;
    transition: 0.2s linear;
    opacity: 0.4;
  }
  &.btn--toggle-theme {
    position: absolute;
    top: 2rem;
    right: 3rem;
    z-index: 1;
    padding: 0;
    border-radius: 50%;
    width: 2.5rem;
    height: 2.5rem;
    > .material-symbols-outlined {
      margin-left: 0;
    }
  }
}
input {
  border: 1px solid #eee;
  line-height: 1;
  padding: 0.75em 2em;
  border-radius: 0.5rem;
  box-shadow: 0 0 0.25rem rgba(93, 43, 255, 0.2);
  margin-right: 0.5em;
  font-size: 14px;
}

@media (max-width: 992px) {
  .btn.btn--toggle-theme {
    top: 1rem;
    right: 1rem;
  }
}

@media (max-width: 768px) {
  input {
    width: 100%;
    margin-right: 0;
    margin-bottom: 0.5rem;
  }
}

@media (max-width: 576px) {
  button,
  .btn {
    width: 100%;
  }
}
